export default {
    data: () => {   
        return {
            Nom: 'undefined',
            Gamme: 'undefined',
            Description: 'undefined',
            Ingredients: [],
            Puht: null,
        }

    },
    methods: {
        async Submit() {
            const body = {
                Nom: this.Nom,
                Gamme: this.Gamme,
                Description: this.Description,
                Puht: this.Puht
            }
            const request = await this.$axios.$post('http://localhost:8000/editModele', body)
            return request
        },
        goBack() {
            this.$router.back()
        }
    },
}
